<template lang="html">
  <form @submit.prevent='FormHandler'>
    <input type="text" placeholder="Description" v-model="FormData.Desc" />
    <input type="number" placeholder="Value..." v-model="FormData.value" />
    <input type="date" placeholder="Date..." v-model="FormData.date" />
    <input type="submit" placeholder="SUBMIT" />
  </form>
</template>

<script>
import { reactive } from "vue";
export default {
  props: {
    state: Object,
  },
  setup(props, { emit }) {
    const FormData = reactive({
      Desc: null,
      value: null,
      date: null,
    });

    function FormHandler() {
      emit("add-Income", {
        Desc: FormData.Desc,
        value: FormData.value,
        date: FormData.date,
      });

      (FormData.Desc = null), (FormData.value = null), (FormData.date = null);
    }



    return {
      FormData,
      FormHandler,
    };
  },
};
</script>

<style lang="css" scoped>
form {
  display: flex;
  justify-content: center;
  margin-top: 30px;
}
input {
  color: #888;
  border: none;
  outline: none;
  font-size: 20px;
}
input::placeholder {
  color: #aaa;
}
input:not([type="submit"]) {
  display: block;
  padding: 5px 15px;
}
form input[type="submit"] {
  display: block;
  padding: 5px 15px;
  font-weight: 500;
  color: #fff;
  background-color: #ffce00;
  cursor: pointer;
}
form input:first-of-type {
  border-radius: 8px 0px 0px 8px;
}
form input:last-of-type {
  border-radius: 0px 8px 8px 0px;
}
</style>
