<template lang="html">
  <div class="income-item">
    <div class="removeItem">x</div>
    <div class="desc">{{ income.desc }}</div>
    <div class="price">{{ income.value }}$</div>
    <div class="date">{{ formattedDate }}</div>
  </div>
</template>

<script>
export default {
  props: {
    income: Object,
  },

  setup(props) {
    let date = new Date(props.income.date);
    let day = date.getDate();
    let month = date.getMonth();
    let year = date.getFullYear();
    let formattedDate = day + "/" + month + "/" + year;

    return {
      formattedDate,
    };
  },
};
</script>

<style lang="css" scoped>
.income-item {
  display:flex;
  padding:15px 15px 15px 15px;
  background-color: #fff;
  border-radius:8px;
  max-width: 600;
  margin:0 auto 30px
}
.removeItem
{
  color:#ef2d2d;
  font-weight: 600;
  font-size: 20px;
  line-height: 1;
  text-align: center;
  margin: 0 15px;
}
.desc
{
  color:#666;
  flex: 1 1 100%;
  font-size: 20px;
}
.price{
  min-width: 400px;
  color:#666;
  font-size: 20px;
}
.date
{
  text-align: right;
  color:#666;
  font-size: 20px;
}

</style>
